import React, { useState, useRef, useEffect } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImageOff } from 'lucide-react';
import { useData } from '../components/DataLoader';
import './Team.css';

const getInitials = (name) => {
  return name
    .split(' ')
    .map(word => word[0])
    .join('')
    .toUpperCase();
};

const getColorFromName = (name) => {
  const colors = [
    '#FF6B6B', '#4ECDC4', '#45B7D1', '#96CEB4', 
    '#FFEEAD', '#D4A5A5', '#9B59B6', '#3498DB'
  ];
  const index = name.split('').reduce((acc, char) => acc + char.charCodeAt(0), 0);
  return colors[index % colors.length];
};

const TeamMemberCard = ({ member, isExpanded, onToggle }) => {
  const descriptionRef = useRef(null);
  const [hasOverflow, setHasOverflow] = useState(false);
  const [imageLoaded, setImageLoaded] = useState(false);
  const [imageError, setImageError] = useState(false);

  useEffect(() => {
    if (descriptionRef.current) {
      const element = descriptionRef.current;
      setHasOverflow(element.scrollHeight > element.clientHeight + 5);
    }
  }, [member.description]);

  const handleCardClick = (e) => {
    if (hasOverflow) {
      onToggle();
    }
  };

  const handleReadMoreClick = (e) => {
    e.stopPropagation();
    onToggle();
  };

  const getImageUrl = (url) => {
    if (url && url.includes('drive.google.com')) {
      if (url.includes('/file/d/')) {
        const fileId = url.split('/file/d/')[1].split('/')[0];

        const newUrl =  `https://drive.google.com/thumbnail?id=${fileId}&sz=s512`;
        console.log("Transformed URL:", newUrl);
        return newUrl;
      }
    }
    return url;
  };


  const handleImageLoad = () => {
    setImageLoaded(true);
    setImageError(false);
  };

  const handleImageError = () => {
    setImageError(true);
    setImageLoaded(false);
  };

  return (

    

    <Card 
      className={`team-card ${isExpanded ? 'expanded' : ''}`}
      onClick={handleCardClick}
    >
      <div className="card-overlay"></div>
      <Card.Body>
        <div className="profile-container">
          {member.image_url ? (
            <>
              {!imageLoaded && !imageError && (
                <div 
                  className="loading-placeholder"
                  style={{ 
                    backgroundColor: '#f0f0f0',
                    width: '100%',  
                    height: '100%',
                    borderRadius: '50%',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center'
                  }}
                >
                  Loading...
                </div>
              )}
              <img
                src={getImageUrl(member.image_url)}
                alt={member.nom}
                className={`profile-image ${imageLoaded ? 'visible' : 'hidden'}`}
                onLoad={handleImageLoad}
                onError={handleImageError}
                style={{ 
                  display: imageError ? 'none' : 'block',
                  opacity: imageLoaded ? 1 : 0,
                  transition: 'opacity 0.3s ease'
                }}
              />
              {imageError && (
                <div
                  className="initials-circle error-state"
                  style={{ 
                    backgroundColor: getColorFromName(member.nom),
                    borderRadius: '50%',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'white'
                  }}
                >
                  <ImageOff size={32} />
                </div>
              )}
            </>
          ) : (
            <div
              className="initials-circle"
              style={{ 
                backgroundColor: getColorFromName(member.nom),
                borderRadius: '50%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                color: 'white',
                fontSize: '2rem'
              }}
            >
              {getInitials(member.nom)}
            </div>
          )}
        </div>
        <Card.Title>{member.nom}</Card.Title>
        <Card.Subtitle className="text-muted">{member.role}</Card.Subtitle>
        {member.description && (
          <div className="description-container">
            <div 
              ref={descriptionRef}
              className={`description-fade ${!isExpanded && hasOverflow ? 'has-fade' : ''}`}
              style={{ 
                maxHeight: isExpanded ? 'none' : '80px',
                overflow: 'hidden'
              }}
            >
              <Card.Text>
                {member.description}
              </Card.Text>
            </div>
            {hasOverflow && !isExpanded && (
              <button
                className="read-more-btn"
                onClick={handleReadMoreClick}
              >
                Lire la suite
              </button>
            )}
          </div>
        )}
      </Card.Body>
    </Card>
  );
};

function Team() {
  const { data, loading, error } = useData();
  const [expandedMember, setExpandedMember] = useState(null);

  if (loading) return <Container className="my-5"><h2>Chargement...</h2></Container>;
  if (error) return <Container className="my-5"><h2>Erreur de chargement</h2></Container>;
  if (!data.team || data.team.length === 0) return <Container className="my-5"><h2>Aucun membre disponible</h2></Container>;
  console.log("Team data:", data?.team);

  return (
    <Container className="my-5">
      <h1>Notre Ã‰quipe</h1>

      <br />
      <Row>
        {data.team.map((member) => (
          <Col md={4} key={member.id} className="mb-4">
            <TeamMemberCard
              member={member}
              isExpanded={expandedMember === member.id}
              onToggle={() => setExpandedMember(
                expandedMember === member.id ? null : member.id
              )}
            />
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Team;